<template>
  <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
      <!-- <div class="center_content_title">
        <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
        <span>{{pageTitle}}</span>
      </div> -->
      <div class="center_content_search">
        <div class="center_content_search_left">
          <div class="search-input-suffix">
            <label>角色搜索：</label>
            <el-input
              class="search"
              @keyup.enter.native="search"
              v-model="page.keywords"
              placeholder="请输入搜索关键词"
              clearable
            ></el-input>
            <!-- <el-button @click="search" type="primary">搜索</el-button> -->
          </div>
          <div class="search-input-suffix" style="margin: 0 0 0 20px">
            <div class="search--btn"  @click="search">搜索</div>
          </div>
        </div>
        <!-- <div class="center_content_search_right">
          <el-button @click="openAdd" type="primary" icon="el-icon-plus" round>新增</el-button>
        </div> -->
      </div>
      <div class="table-container">
        <el-table :data="tableData" stripe ref="multipleTable" style="width: 100%">
          <el-table-column prop="name" label="角色名称"></el-table-column>
          <el-table-column prop="id" label="角色ID"></el-table-column>
          <el-table-column prop="creatorVal" label="创建人"></el-table-column>
          <el-table-column label="操作" width="330">
          <template slot-scope="scope">
              <div class="operate-button distance">
                  <SvgIcon icon-class="iconbianji1"></SvgIcon>
                  <el-button
                  @click.native.prevent="editRow(scope.row)"
                  type="text"
                  size="small">
                  编辑
                  </el-button>
              </div>
              <div class="operate-button">
                  <SvgIcon icon-class="iconshanchu"></SvgIcon>
                  <el-button
                  @click.native.prevent="deleteRow(scope.row)"
                  type="text"
                  size="small"
                  style="color: #ff5858">
                  删除
                  </el-button>
              </div>
              <div class="operate-button distance">
                <SvgIcon icon-class="iconshouquan" size="15px"></SvgIcon>
                <el-button
                  @click.native.prevent="authorizationRow(scope.row)"
                  type="text"
                  size="small"
                >目录授权</el-button>
              </div>
              <div class="operate-button">
                <SvgIcon icon-class="iconshouquan" size="15px"></SvgIcon>
                <el-button
                  @click.native.prevent="distributionRow(scope.row)"
                  type="text"
                  size="small"
                >可分配授权</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="center_pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page="page.page"
            :page-sizes="[15,30,40,50]"
            :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
            :total="data.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      :before-close="handleDialogClose"
      class="set-el-dialog"
      :close-on-click-modal="false"
      width="30%"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="角色名" prop="name">
          <el-input v-model="form.name" placeholder="请输入角色名"></el-input>
        </el-form-item>
        <el-form-item label="所属学院" prop="academyId">
          <el-select v-model="form.academyId" placeholder="请选择所属学院" style="width: 100%" :disabled="!(roleName === '学校管理员') && !(roleName === '系统管理员')">
            <el-option v-for="item in academyOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属专业" prop="specialtyId">
          <el-select v-model="form.specialtyId" placeholder="请选择所属专业" style="width: 100%">
            <el-option v-for="item in specialtyOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="create('form')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 授权 -->
    <el-dialog
      title="角色授权"
      :visible.sync="authorizationDialogFormVisible"
      :before-close="handleAuthorizationDialogClose"
      class="set-el-dialog authorization-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="authorizationForm" :rules="authorizationRules" ref="authorizationForm">
        <el-form-item prop="menuIDs">
          <el-input placeholder="输入关键字搜索" v-model="filterText"></el-input>
          <el-tree
            class="filter-tree"
            :data="authorizationTree"
            :props="defaultProps"
            node-key="id"
            @check-change="checkChangeTree"
            show-checkbox
            default-expand-all
            :filter-node-method="filterNode"
            ref="tree"
          ></el-tree>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAuthorization('authorizationForm')">取 消</el-button>
        <el-button type="primary" @click="createAuthorization('authorizationForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 可分配权限 -->
    <el-dialog
      title="可分配授权"
      :visible.sync="distributionDialogFormVisible"
      class="set-el-dialog authorization-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="distributionForm" :rules="distributionRules" ref="distributionForm">
        <el-form-item prop="menuIDs">
          <el-input placeholder="输入关键字搜索" v-model="filterText1"></el-input>
          <el-tree
            class="filter-tree"
            :data="distributionTree"
            :props="defaultProps"
            node-key="id"
            @check-change="checkChangeTree1"
            show-checkbox
            default-expand-all
            :filter-node-method="filterNode1"
            ref="tree1"
          ></el-tree>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="distributionDialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="createDistribution('distributionForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import systemApi from "@/api/systemApi";
export default {
  name: "RoleManagement",
  data() {
    return {
      pageTitle: "角色管理",
      isAdd: true,
      data: {},
      tableData: [],
      userDialogFormVisible: false,
      authorizationDialogFormVisible: false,
      distributionDialogFormVisible: false,
      form: {
        name: "",
        id: undefined,
        specialtyId: '',
        academyId: ''
      },
      page: {
        keywords: "",
        page: 1,
        rows: 15
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入角色名称",
            trigger: "blur"
          }
        ],
        academyId: [
          { required: true, message:'请选择所属学院', trigger: 'blur' }
        ],
        specialtyId: [
          { required: true, message:'请选择所属专业', trigger: 'blur' }
        ]
      },
      authorizationForm: {
        roleID: "",
        menuIDs: []
      },
      distributionForm: {
        roleID: "",
        menuIDs: []
      },
      authorizationRules: {
        menuIDs: [
          {
            required: true,
            message: "请选择资源",
            trigger: "blur"
          }
        ]
      },
      distributionRules: {
        menuIDs: [
          {
            required: true,
            message: "请选择资源",
            trigger: "blur"
          }
        ]
      },
      filterText: "",
      filterText1: "",
      authorizationTree: [],
      distributionTree: [],
      defaultProps: {
        children: "children",
        label: "name"
      },
      roleName: '',
      academyId: '',
      specialtyId: '',
      academyOptions: '',
      specialtyOptions: '',
      flag: false
    };
  },
  created() {},
  mounted() {
    this.initData();
    this.sysMenuAll();
    this.distributionAll();
    this.roleName = localStorage.getItem('roleName')
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    filterText1(val) {
      this.$refs.tree1.filter(val);
    },
    academyIdOfForm(val) {
      if(val) {
        if(this.flag) {
          this.form.specialtyId = ''
        }
        this.getSpecialty(val)
      }else {
        this.specialtyOptions = [
          {
            id: '',
            name: '请先选择所属学院'
          }
        ]
        this.form.specialtyId = ''
      }
    }
  },
  computed: {
    dialogTitle() {
      return this.isAdd ? "添加角色" : "编辑角色";
    },
    academyIdOfForm() {
      return this.form.academyId
    }
  },
  methods: {
    getacademy() {
      this.bodyLoading = true
      this.$get('/basic/academy/all?type=0').then(res => {
        if(res.status === 1) {
          this.academyOptions = res.data
          this.academyOptions.push({
            id: '0',
            name: '无'
          })
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取所属学院失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    getSpecialty(academyID) {
      this.bodyLoading = true
      this.$get(`/basic/specialty/all/${academyID}`).then(res => {
        if(res.status === 1) {
          this.specialtyOptions = res.data
          this.specialtyOptions.push({
            id: '0',
            name: '无'
          })
          // this.specialtyOptions.unshift({
          //   id: '',
          //   name: '请选择'
          // })
          this.flag = true
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取所属班级失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    initData() {
      const url = systemApi.sysRoleList;
      this.$post(url, this.page)
        .then(res => {
          if (res.status === 1) {
            this.data = res;
            this.tableData = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    sysMenuAll() {
      const url = "system/sysMenu/allDist";
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            const menuArr = res.data;
            // menuArr.forEach(item =>{
            //   if(item.children) {
            //     item.children.forEach(vo =>{
            //        vo.children=null;
            //      })
            //   }

            //  })
            this.authorizationTree = menuArr;
            //  this.distributionTree = menuArr
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    distributionAll() {
      const url = "system/sysMenu/allDist";
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            const menuArr = res.data;
            // menuArr.forEach(item =>{
            //   if(item.children) {
            //     item.children.forEach(vo =>{
            //        vo.children=null;
            //      })
            //   }

            //  })
            //  this.authorizationTree=menuArr;
            this.distributionTree = menuArr;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    sysMenuRole(id) {
      const url = systemApi.sysMenuRole({
        roleID: id,
        type: 1
      });
      this.$get(url).then(res => {
        if (res.status === 1) {
          const treeMenuIds = res.data || [];
          this.authorizationDialogFormVisible = true;
          this.authorizationTree.forEach(item => {
            treeMenuIds.forEach((vo, index) => {
              if (item.id === vo) {
                treeMenuIds.splice(index, 1);
              }
            });
          });
          this.authorizationTree.forEach(item => {
            if(item.children){
                item.children.forEach(item1 => {
                  treeMenuIds.forEach((vo, index) => {
                    if (item1.id == vo && item1.children) {
                      treeMenuIds.splice(index, 1);
                    }
                  });
                  
                });
            }
           
          });

          this.$nextTick(() => {
            this.$refs.tree.setCheckedKeys(treeMenuIds);
          });
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    distributionRole(id) {
      this.distributionDialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["distributionForm"].resetFields();
      });

      const url = systemApi.sysMenuRole({
        roleID: id,
        type: 2
      });
      this.$get(url).then(res => {
        if (res.status === 1) {
          const treeMenuIds = res.data || [];
          this.distributionTree.forEach(item => {
            treeMenuIds.forEach((vo, index) => {
              if (item.id === vo) {
                treeMenuIds.splice(index, 1);
              }
            });
          });

          this.distributionTree.forEach(item => {
            item.children.forEach(item1 => {
              treeMenuIds.forEach((vo, index) => {
                if (item1.id == vo && item1.children) {
                  treeMenuIds.splice(index, 1);
                }
              });
              
            });
          });
          this.$nextTick(() => {
            this.$refs.tree1.setCheckedKeys(treeMenuIds);
          });
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    headInfo(item) {
      this.pageTitle = item[1];
    },
    authorizationRow(row) {
      this.sysMenuRole(row.id);
      this.authorizationForm.roleID = row.id;
    },
    distributionRow(row) {
      this.distributionForm.roleID = row.id;
      this.distributionRole(row.id);
    },
    checkChangeTree(row, check) {
      const self = this;
      self.authorizationForm.menuIDs = self.$refs.tree.getCheckedKeys();
    },
    checkChangeTree1(row, check) {
      const self = this;
      self.distributionForm.menuIDs = self.$refs.tree1.getCheckedKeys();
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },
    filterNode1(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },
    createAuthorization(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let menuIdArr = JSON.parse(
            JSON.stringify(this.authorizationForm.menuIDs)
          );
          const authorizationTree = this.authorizationTree;
          // for ( let i = 0, lengths = authorizationTree.length; i < lengths; i++ ) {
          //   const list = authorizationTree[i].children;
          //   for (let j = 0, length = list.length; j < length; j++) {
          //     for (let k = 0, leng = menuIdArr.length; k < leng; k++) {
          //       if (list[j].id === menuIdArr[k]) {
          //         menuIdArr.push(list[j].parentId);
          //         break;
          //       }
          //     }
          //   }
          // }
          // for (let i = 0, lengths = authorizationTree.length; i < lengths; i++) {
          //   const list = authorizationTree[i].children;
          //   for (let j = 0, length = list.length; j < length; j++) {
          //     if(list[j].children && list[j].children.length > 0) {
          //       const list1 = list[j].children;
          //       for (let l = 0; l < list1.length; l++) {
          //         for (let k = 0, leng = menuIdArr.length; k < leng; k++) {
          //           if (list1[l].id === menuIdArr[k]) {
          //             menuIdArr.push(list1[l].parentId);
          //             break;
          //           }
          //         }
          //       }
          //     }
          //   }
          // }
          menuIdArr = menuIdArr.concat(this.$refs.tree.getHalfCheckedKeys())

           menuIdArr=this.unique(menuIdArr)
           const postData={
                 roleID:this.authorizationForm.roleID,
                 menuIDs:menuIdArr,
                 type: 1
           }

          this.authorizationAdd(postData)
        }
      });
    },
    createDistribution(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let menuIdArr = JSON.parse(
            JSON.stringify(this.distributionForm.menuIDs)
          );
          const distributionTree = this.distributionTree;

          // for (let i = 0, lengths = distributionTree.length; i < lengths; i++) {
          //   const list = distributionTree[i].children;
          //   for (let j = 0, length = list.length; j < length; j++) {
          //     for (let k = 0, leng = menuIdArr.length; k < leng; k++) {
          //       if (list[j].id === menuIdArr[k]) {
          //         menuIdArr.push(list[j].parentId);
          //         break;
          //       }
          //     }
          //   }
          // }
          
          // for (
          //   let i = 0, lengths = distributionTree.length;
          //   i < lengths;
          //   i++
          // ) {
          //   const list = distributionTree[i].children;
          //   for (let j = 0, length = list.length; j < length; j++) {
          //     if(list[j].children && list[j].children.length > 0) {
          //       const list1 = list[j].children;
          //       for (let l = 0; l < list1.length; l++) {
          //         for (let k = 0, leng = menuIdArr.length; k < leng; k++) {
          //           if (list1[l].id === menuIdArr[k]) {
          //             menuIdArr.push(list1[l].parentId);
          //             break;
          //           }
          //         }
          //       }
          //     }
          //   }
          // }
          
          menuIdArr = menuIdArr.concat(this.$refs.tree1.getHalfCheckedKeys())
          menuIdArr = this.unique(menuIdArr);
          const postData = {
            roleID: this.distributionForm.roleID,
            menuIDs: menuIdArr,
            type: 2
          };

          this.distributionAdd(postData);
        }
      });
    },
    cancelAuthorization(formName) {
      this.authorizationDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    handleAuthorizationDialogClose() {
      this.$refs["authorizationForm"].resetFields();
      this.authorizationDialogFormVisible = false;
    },
    openAdd() {
      this.userDialogFormVisible = true;
      this.form = {
        name: "",
        id: undefined,
        academyId: '',
        specialtyId: ''
      };
      if(localStorage.getItem('roleName') === '学院管理员') {
        this.form.academyId = localStorage.getItem('academyId')
      }
      this.getacademy()
      this.isAdd = true;
    },
    search() {
      this.resetTemp();
      this.initData();
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    },
    create(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.isAdd) {
            this.userAdd();
          } else {
            this.userEdit();
          }
        }
      });
    },
    cancel(formName) {
      this.userDialogFormVisible = false;
      setTimeout(() => {
        this.$refs["form"].resetFields();
      }, 500)
    },
    handleDialogClose() {
      this.userDialogFormVisible = false;
      setTimeout(() => {
        this.$refs["form"].resetFields();
      }, 500)
    },
    deleteRow(row) {
      this.$confirm("确定删除该角色?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.userDel(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    editRow(row) {
      this.flag = false
      const obj = {
        name: row.name,
        id: row.id,
        specialtyId: row.specialtyId || '',
        academyId: row.academyId
      };
      if(localStorage.getItem('roleName') === '学院管理员') {
        this.form.academyId = localStorage.getItem('academyId')
      }
      this.form = obj;
      this.getacademy()
      this.userDialogFormVisible = true;
      this.isAdd = false;
    },
    authorizationAdd(postData) {
      const url = systemApi.sysRoleGrant;
      this.$post(url, postData)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.authorizationDialogFormVisible = false;
            this.resetTemp();
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    distributionAdd(postData) {
      const url = systemApi.sysRoleGrant;
      this.$post(url, postData)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.distributionDialogFormVisible = false;
            this.resetTemp();
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userAdd() {
      const url = systemApi.sysRoleAdd;
      this.$post(url, this.form)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.resetTemp();
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userEdit() {
      const url = systemApi.sysRoleEdit;
      this.$put(url, this.form)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userDel(id) {
      const url = systemApi.sysRoleDel({
        id: id
      });
      this.$deletes(url)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    resetTemp() {
      this.page.page = 1;
      this.page.rows = 15;
    },
    unique(arr) {
      //数组去重
      var h = {};
      var temp = [];
      for (var i = 0; i < arr.length; i++) {
        if (!h[arr[i]]) {
          h[arr[i]] = true;
          temp.push(arr[i]);
        }
      }

      return temp;
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.authorization-dialog {
  .el-tree {
    height: 364px;
    border: 1px solid #dcdfe6;
    overflow-y: auto;
    border-radius: 4px;
    margin-top: 5px;
  }
}
</style>



